/* EventPage.css */

/* Container for housesa and housesb */
.house {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-bottom: 400px;
  
}

/* housesa and housesb */
.housesa,
.housesb {
  display: flex;
  justify-content: center;
  
}

.h1{

  font-family: sudhan;
  font-size: 50px;
}
@font-face {
  font-family: sudhan;
  src:url(../assets/Harry_Potter/PlayfairDisplay-Regular.ttf);
}
/* Vertical white line between house1 and house2 */
.white-line {
  width: 2px; /* Thickness of the line */
  height: 300px; /* Height of the line */
  background-color: white; /* Color of the line */
  margin: 0 100px; /* Space around the line */
}

/* Horizontal divider between housesa and housesb */
.divider {
  display: flex;
  justify-content: center;
  align-items: center;
  margin: 2rem 0; /* Space above and below the divider */
}

/* EventPage.css */

.divider img {
  width: 900px; /* Size of the plus-like divider */
  mask: linear-gradient(
    to right,
    transparent, /* Dim effect at the start */
    white,       /* Bright effect in the center */
    transparent  /* Dim effect at the end */
  );
  -webkit-mask: linear-gradient(
    to right,
    transparent, /* Dim effect at the start */
    white,       /* Bright effect in the center */
    transparent  /* Dim effect at the end */
  );
}
/* Paper card styling */
.paper {
  height: 300px;
  width: 200px;
  border-radius: 20px;
  background-repeat: no-repeat;
  background-size: cover;
  display: flex;
  flex-direction: column;
  align-items: center;
  text-align: center;
  color: white;
  font-size: 18px;
  font-weight: bold;
  position: relative;
  overflow: hidden;
  transition: all 0.3s ease-in-out;
}

.paper h3 {
  position: absolute;
  bottom: 20%; /* Initially above middle */
  transition: bottom 0.3s ease-in-out;
}

/* Moves text to the top on hover */
.paper:hover h3 {
  bottom: 40%; /* Moves text to the top */
}

.paper button {
  position: absolute;
  bottom: -50px; /* Initially hidden below */
  padding: 8px 16px;
  background-color: #d4af37; /* Gold button */
  border: none;
  color: black;
  font-weight: bold;
  cursor: pointer;
  border-radius: 5px;
  opacity: 0; /* Initially invisible */
  transition: bottom 0.3s ease-in-out, opacity 0.3s ease-in-out;
}

/* Show button smoothly when hovered */
.paper:hover button {
  bottom: 20px; /* Moves button up */
  opacity: 1;
}
/* EventPage.css */

/* Vertical white line with gradient effect */
.white-line {
  width: 2px; /* Thickness of the line */
  height: 500px; /* Height of the line */
  background: linear-gradient(
    to bottom, 
    rgba(255, 255, 255, 0.3), /* Dim effect at the start (30% opacity) */
    rgba(255, 255, 255, 1)    /* Bright effect at the end (100% opacity) */
  );
  margin: 0 100px; /* Space around the line */
  margin-top: 220px;
  margin-right: 120px;
}
.white-line1 {
  width: 2px; /* Thickness of the line */
  height: 500px; /* Height of the line */
